	module.exports = {"data":{"challengeNode":{"title":"Add Gaps Faster with grid-gap","description":["<code>grid-gap</code>是<code>grid-row-gap</code>和<code>grid-column-gap</code>的简写，它更方便使用。如果<code>grid-gap</code>有一个值，行与行之间和列与列之间将添加等于该值的间隙。但是，如果有两个值，第一个值将作为行间隙的高度值，第二个值是列间隙的宽度值。","<hr>","使用<code>grid-gap</code>在行之间添加<code>10px</code>的间隙，在列之间添加<code>20px</code>的间隙。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/css-grid/add-gaps-faster-with-grid-gap","blockName":"CSS Grid","tests":[{"text":"<code>container</code>类应该有<code>grid-gap</code>属性，在行之间引入<code>10px</code>的间隙，在列之间引入<code>20px</code>的间隙。","testString":"assert(code.match(/.container\\s*?{[\\s\\S]*grid-gap\\s*?:\\s*?10px\\s*?20px\\s*?;[\\s\\S]*}/gi), '<code>container</code>类应该有<code>grid-gap</code>属性，在行之间引入<code>10px</code>的间隙，在列之间引入<code>20px</code>的间隙。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  .d1{background:LightSkyBlue;}\n  .d2{background:LightSalmon;}\n  .d3{background:PaleTurquoise;}\n  .d4{background:LightPink;}\n  .d5{background:PaleGreen;}\n  \n  .container {\n    font-size: 40px;\n    min-height: 300px;\n    width: 100%;\n    background: LightGray;\n    display: grid;\n    grid-template-columns: 1fr 1fr 1fr;\n    grid-template-rows: 1fr 1fr 1fr;\n    /* 请在本行以下添加你的代码 */\n    \n    \n    /* 请在本行以上添加你的代码 */\n  }\n</style>\n<div class=\"container\">\n  <div class=\"d1\">1</div>\n  <div class=\"d2\">2</div>\n  <div class=\"d3\">3</div>\n  <div class=\"d4\">4</div>\n  <div class=\"d5\">5</div>\n</div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/css-grid/use-grid-column-to-control-spacing","id":"5a9036ee38fddaf9a66b5d37"},"slug":"/responsive-web-design/css-grid/add-gaps-faster-with-grid-gap"}}

